import React from 'react'
import './Welcome.scss'
import { NavLink } from 'react-router-dom'

const Welcome = () => {
    return (
        <div className='welcome'>
            <div className='welcome__title'> !</div>
            <div className='welcome__text'>     .</div>
            <div className='welcome__warning'> !   .</div>
            <NavLink to={'/login'} className="welcome__login"></NavLink>
        </div>
    )
}

export default Welcome
